.bottom {
    position: relative;
    background: color(grey, dark);
    display: flex;
    justify-content: flex-end;
    width: 100%;
    
    &__main-controls {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 20px 0 15px;
        flex-grow: 1;
    }

    &__controls {
        display: flex;
        
        &:not(:last-child) {
            margin-bottom: 10px;
        }

    }

    &__control-item {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 45px;
        height: 45px;
        color: color(grey, light);
        transition: color .3s ease;

        &:not(:last-child) {
            margin-right: 15px;
        }

        &:hover {
            color: white;
        }

        svg {
            width: 15px;
            height: 15px;    
        }

        &--play,
        &--pause {
            border-radius: 50%;
            border: 1px solid color(grey, light);
            transition: border-color .3s ease;
            
            &:hover {
                border-color: white;
            }

        }

        &--play {
            
            .is-playing & {
                display: none;
            }
            
        }
        
        &--pause {
            display: none;

            .is-playing & {
                display: flex;
            }

        }

        &--shuffle {
            
            .is-shuffled & {
                color: white;
            }

        }
        
        &--repeat {
            
            .is-repeat & {
                color: white;
            }

        }

    }

    &__progress {
        display: flex;
        align-items: center;
        width: 700px;

        input {
            width: 100%;
        }

        &:not(:last-child) {
            margin-right: 10px;
        }

    }
    
    &__current,
    &__duration {
        font-size: 12px;
        color: #afada8;
    }

    &__progress-bar {
        flex-grow: 1;
    }

    &__progress-bar,
    &__current {
        
        &:not(:last-child) {
            margin-right: 20px;
        }

    }
    
    &__duration {}

    &__volume {
        border-left: 1px solid color(grey, darker);
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 200px;
        padding: 30px 25px;
    }

}